import React from "react";
import './App.css';

const array = [];

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      value: "",
      msg: "Add a word"
    }
    this.getValue = this.getValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  getValue(event){
    this.setState({
      value: event.target.value 
    });
  }

  handleSubmit(){
    const text = this.state.value;
    let message = "";
    if (array.includes(text) === false) {
      array.push(text);
      message = "Word is add";
    } else {
      message = "Word is exist";
    }
    this.setState({
      msg: message
    });
  }
  render() {
    return(
      <div className="App">
        <div>
          <h1>Dictionary</h1>
          <input type="text" onChange={this.getValue} />
          <button onClick={this.handleSubmit}>Search</button>
          <p>{this.state.msg}</p>
        </div>
        {array.map(txt => <p key={txt}>{txt}</p>)}
      </div>
    );
  }
}

export default App;